import './ToggleButton.css';
import React, { useState } from 'react';
import { useColor } from '../ColorContext';
import sunwithe from '../images/sunwithe.svg'
import sunblack from '../images/sunblack.svg'
import moon from '../images/moon.svg'

function ToggleButton() {
    const { color, setColor } = useColor();
    const [toggle, setToggle] = useState(false);

    const handleToggle = () => {
        setToggle(!toggle);
        setColor(toggle ? '#3C3C56' : '#e6f5ff');

        const newLogo = toggle ? 'logo1' : 'logo';

    };

    const buttonStyle = {
        backgroundColor: color,
        color: toggle ? 'white' : 'black',
        transition: 'background-color 1s, color 1s',
    };
    const headerStyle = {
        backgroundColor: color === '#3C3C56' ? color : 'white',
        color: color === '#3C3C56' ? 'white' : 'black',
        boxShadow: '0px 5px 10px rgba(0, 0, 0, 0.2)',
        zIndex: 1,

        // другие стили заголовка
    };

    return (
        <div
            className={`wrg-toggle ${toggle ? 'wrg-toggle--checked' : ''}`}
            style={buttonStyle}
            onClick={handleToggle}
        >
            <div className="wrg-toggle-container" >
                <div className="wrg-toggle-uncheck">
                    <span><img className='imagesTogg' src={color === '#3C3C56' ? sunblack : sunwithe} alt="Logo" /></span>
                </div>
                <div className="wrg-toggle-check" >
                    <span><img className='imagesTogg' src={moon} alt="Logo" /></span>
                </div>
                <div className="wrg-toggle-circle"></div>
            </div>

        </div>
    );
}

export default ToggleButton;
