import React from 'react';
import { useSelector} from 'react-redux';
import {termsOfUse, Instruction, come} from './Text';
import './Instructions.css';
import {useColor} from "../ColorContext";

function Instructions() {

    const currentLanguage = useSelector((state) => state.language.currentLanguage);
    const { color } = useColor();
    const titleStyle = {
        color: color === '#3C3C56' ? 'white' : '#7B6CEA',
    }
    return (
        <div className="instructions">
            <h1 style={titleStyle}>{Instruction[currentLanguage].items[0].text}</h1>
            <h2>{Instruction[currentLanguage].items[1].text}</h2>
            <span>
             {termsOfUse[currentLanguage].items[0].text}
            </span>
            <span>
             {termsOfUse[currentLanguage].items[1].text}
            </span>
            <span>
             {termsOfUse[currentLanguage].items[2].text}
            </span>
            <span>
             {termsOfUse[currentLanguage].items[3].text}
            </span>
            <span>
             {termsOfUse[currentLanguage].items[4].text}
            </span>
            <span>
             {termsOfUse[currentLanguage].items[5].text}
            </span>
            <span>
             {termsOfUse[currentLanguage].items[6].text}
            </span>
            <span>
             {termsOfUse[currentLanguage].items[7].text}
            </span>
            <div className='instr'>
            <span >
             {termsOfUse[currentLanguage].items[8].text}
            </span>
        </div>
            <div className='instr'>
            <span  >
             {termsOfUse[currentLanguage].items[9].text}
            </span>
            </div>
                <div className='buttonsubmit'>
                        <button type="submit">{come[currentLanguage].items[1].text}</button>
                </div>
        </div>
    );
}

export default Instructions;
