import './WelcomePage.css';
import Untitled from '../images/Untitled.svg'
import Modal from './Modal'; // Путь к компоненту Modal
import RegistrationForm from './RegistrationForm'; // Путь к компоненту RegistrationForm
import React, {useState} from 'react';
import Login from './Login';
import {useSelector} from 'react-redux';
import {mission} from './Text';
import {buut} from './Text';
import {useColor} from "../ColorContext";
import mobpk from '../images/mobpk.svg'

function WelcomePage() {
    const currentLanguage = useSelector((state) => state.language.currentLanguage);

    const [isLoginModalOpen, setLoginModalOpen] = useState(false);
    const [isRegistrationModalOpen, setRegistrationModalOpen] = useState(false);

    const {color} = useColor();

    const headerStyle = {
        backgroundColor: color,
        color: color === '#3C3C56' ? '#e6f5ff' : 'black', // Если фон #3C3C56, то текст белый, иначе черный
        // другие стили заголовка
        transition: 'background-color 1s, color 1s, box-shadow 1s',
    };
    const headerTextStyle = {
        color: color === '#3C3C56' ? 'white' : 'black',
        transition: 'background-color 1s, color 1s',
        // другие стили для текста заголовка
    };

    const openLoginModal = (e) => {
        e.preventDefault()
        setLoginModalOpen(true);
    };

    const openRegistrationModal = (e) => {
        e.preventDefault()
        setRegistrationModalOpen(true);
    };
    const headerButStyle = {
        backgroundColor: color === '#3C3C56' ? 'white' : '#00B9F8',
        color: color === '#3C3C56' ? '#00B9F8' : 'white',
        transition: 'background-color 1s, color 1s, box-shadow 1s',
    };


    return (
        <div className="WelcomePage" style={headerStyle}>
            <div className='Info'>
                <div className='Info-left'>
                    <div className='Info-text' style={headerTextStyle}>
                        <span>{mission[currentLanguage].header}</span>
                    </div>
                    <div className='registration'>
                        <button className='come' onClick={openLoginModal}
                                style={headerButStyle}>{buut[currentLanguage].items[0].text}</button>
                        <button className='Become-a-member'
                                onClick={openRegistrationModal}>{buut[currentLanguage].items[1].text}</button>
                    </div>

                </div>
                <div className='Info-right'>
                    <div className="Untitled">
                        <img className='fullpkimg' src={Untitled} alt="Untitled"/>
                        <img className='mobpkimg' src={mobpk} alt="mobpk"/>
                    </div>
                </div>
            </div>

            {isLoginModalOpen && (
                <Modal isOpen={isLoginModalOpen} onClose={() => setLoginModalOpen(false)}>
                    <Login/>
                </Modal>
            )}

            {isRegistrationModalOpen && (
                <Modal isOpen={isRegistrationModalOpen} onClose={() => setRegistrationModalOpen(false)}>
                    <RegistrationForm/>
                </Modal>
            )}

        </div>
    );
}

export default WelcomePage;